@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR);
body, html, #root {
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	transition: color 0.4s;
	background: linear-gradient(-55deg, rgb(249, 212, 219),rgb(220, 228, 250));
	background: var(--background-gradi);
	color: black;
	color: var(--font-color);
	font-family: 'Noto Sans KR', sans-serif;
}

header {
	padding: 1rem 0rem;
	display: -webkit-flex;
	display: flex;
}
header * {
	margin: auto;
}
header .Title{
	font-size: 2rem;
	font-weight: bold;
}
header .Title + span {
	-webkit-flex: .5 1;
	        flex: .5 1;
}

.ChatBox {
	margin: auto;
	max-height: 80%;

	width: 90%;

	overflow: scroll;
}

::-webkit-scrollbar {
	display: none;
}

@media screen and (max-width: 1200px) {
	.ChatBox {
		width: 95%;
	}
}
