:root {
    --background-gradi: linear-gradient(-55deg, rgb(249, 212, 219),rgb(220, 228, 250));
	--font-color: black;
}
[data-theme="dark"] {
    --background-gradi: linear-gradient(-55deg, rgb(106, 44, 102),rgb(47, 55, 88));
	--font-color: white;
}
.DarkModSwitch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.DarkModSwitch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.Slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(255, 183, 197);
    transition: .4s;
}

.Slider::before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
}

input:checked + .Slider {
    background-color: rgb(148, 53, 142);
}

input:focus + .Slider {
    box-shadow: 0 0 1px #2196F3;
}

input:checked + .Slider::before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px);
}

.Slider.round {
    border-radius: 34px;
}
.Slider.round::before {
    border-radius: 50%;
}
.Message {
    margin: 3.5% 0;
    display: -webkit-flex;
    display: flex;

    font-size: 0.9rem;

    -webkit-animation-duration: 1s;

            animation-duration: 1s;
    -webkit-animation-name: MessageArrived;
            animation-name: MessageArrived;
}

.MsgBefore {
    background-color: rgb(195, 195, 195);
    width: 5px;
    margin-right: 0.8rem;
    border-radius: 1rem;
}
.MsgBefore.Black {
    background-color: rgb(65, 65, 65);
    background-color: var(--background-color);
}
.Msg {
    max-width: 90%;
}

select {
    -webkit-appearance: none;
            appearance: none;
    background-color: rgba(0, 0, 0, 0);
    color: var(--font-color);
    border-color: var(--font-color);
    border-radius: 0.3rem;
    transition: color 0.4s;
    
    padding: 0.1rem 0.3rem;
    /* line-height: 1.2rem; */
}
option {
    color: black;
}
.UserSubmit {
    cursor: pointer;
    color: rgb(3, 116, 219);
    font-size: 0.8rem;
    font-weight: bold;
}
.UserSubmit::before {
    content: " ";
}


@-webkit-keyframes MessageArrived {
    from {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
        opacity: 0.2;
    }
    to {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        opacity: 1;
    }
}


@keyframes MessageArrived {
    from {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
        opacity: 0.2;
    }
    to {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        opacity: 1;
    }
}

@media screen and (min-width: 1000px) {
    .Message {
        margin: 1.5% 0;
    }
    .Message span {
        padding: 0.2rem 0;
    }    
}
